@media (min-width: 1024px) {
    .slider-item__text {
        font-size: 45px; }
    .support__item {
        width: 230px;
        height: 146px;
        margin-bottom: 14px; }
    .advantages__num {
        font-size: 36px; }
    .advantages__description {
        margin-top: 13px;
        font-size: inherit; }
    .services-list__item:nth-child(2n+2) {
        top: 81px; } }
@media (max-width: 1024px) {
    .contacts, .header__nav, .sidebar {
        display: none; }
    .menu-btn {
        display: block; }
    body {
        background: none; }
    .services__block {
        background: url(../img/services-block.jpg) no-repeat;
        height: 471px;
        margin-top: 45px; }
    .content__text {
        padding: 0 100px; }
    .support__list {
        margin: 0;
        max-width: 100%;
        text-align: center; }
    .services-list {
        width: 445px;
        height: 100%;
        margin: 0 auto; }
    .services-list__item:nth-child(2) {
        top: 81px; }
    .services-list__item:nth-child(4) {
        top: 18px; }
    .services-list__item:nth-child(5) {
        top: 18px;
        left: 136px; } }
@media (max-width: 768px) {
    .content__text {
        padding: 0 50px; }
    .about {
        flex-direction: column; }
    .header {
        height: 125px;
        &__container {
            line-height: 125px; } }
    .menu-btn {
        margin: 40px 0; }
    .contacts__form {
        padding: 0 20px;
        float: unset; }
    .contacts__text {
        float: unset; }
    .slider-item__img {
        height: 388px; }
    .slider-item__text {
        left: 5px;
        font-size: 25px; }
    .slider-dots {
        position: unset;
        top: 10px; } }
@media (max-width: 425px) {
    .content__text {
        padding: 0 15px; }
    .slider-dots {
        min-width: 350px; }
    .contacts__block {
        padding: 0;
        background: #fff;
        width: 100%;
        top: 0;
        transform: translate(-50%, 0); }
    .contacts__item {
            padding: 0 15px; } }

@media (max-width: 480px) {
    .services-list {
        width: 300px; }
    .services-list__item:nth-child(2n+2) {
        top: 78px; }
    .services-list__item:nth-child(2n+1) {
        margin-bottom: 10px; }
    .services__block {
        height: 500px;
        background-size: cover; }
    .services-list__item:nth-child(5) {
        left: unset;
        top: unset; } }
