html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0; }

body {
    background: #203b4e url("../img/body-bg.jpg") no-repeat;
    display: flex;
    min-width: 300px; }


.wrapper {
    flex: 1;
    width: 100%;
    height: 100%; }


.popup {
    position: fixed;
    width: 100%;
    height: 100%; }

ul {
    list-style: none; }

h1,h2,h3,h4,h5 {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: normal; }
p, ul, li {
    padding: 0;
    margin: 0; }
a {
    text-decoration: none; }
textarea {
    resize: none; }
div, input, textarea, select, button, span, a:active {
    outline: none; }
.main-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    background: url(../img/sidebar-bg.jpg); }

.page-main {
    flex: 3;
    color: #373737;
    background: #fff;
    &__wrapper {}
    display: flex;
    flex-direction: column;
    height: 100%;
    section {
        flex: 1; } }


.icon {
    background: url(../img/sprite.png);
    display: inline-block;
    &_lift {
        background-position: -468px -59px;
        width: 20px;
        height: 20px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }
    &_advantages-01 {
        background-position: -78px -290px;
        width: 74px;
        height: 74px; }
    &_advantages-02 {
        background-position: -230px -290px;
        width: 74px;
        height: 74px; }
    &_advantages-03 {
        background-position: -154px -290px;
        width: 74px;
        height: 74px; }
    &_mail {
        background-position: -468px -193px;
        width: 18px;
        height: 14px;
        margin-right: 10px;
        &-blue {
            background-position: -468px -153px;
            width: 20px;
            height: 16px; } }
    &_adress {
        background-position: -468px -171px;
        width: 14px;
        height: 20px; }
    &_phone {
        background-position: -468px -105px;
        width: 15px;
        height: 25px;
        margin-right: 10px;
        &-blue {
            background-position: -468px -132px;
            width: 19px;
            height: 19px; } }
    &_support-1 {
        background-position: -423px -51px;
        width: 39px;
        height: 39px;
        transform: translate(-46%, -46%);
        position: absolute;
        top: 50%;
        left: 50%; }
    &_support-2 {
        background-position: 0px -380px;
        width: 45px;
        height: 45px;
        transform: translate(-49%, -53%);
        position: absolute;
        top: 50%;
        left: 50%; }
    &_support-3 {
        background-position: -423px -92px;
        width: 35px;
        height: 43px;
        transform: translate(-52%, -49%);
        position: absolute;
        top: 50%;
        left: 50%; }
    &_support-4 {
        position: absolute;
        top: 50%;
        left: 50%;
        background-position: -142px -380px;
        width: 44px;
        height: 40px;
        transform: translate(-50%, -48%); }
    &_support-5 {
        position: absolute;
        top: 50%;
        left: 50%;
        background-position: -47px -380px;
        width: 45px;
        height: 45px;
        transform: translate(-51%, -51%); }
    &_support-6 {
        position: absolute;
        top: 50%;
        left: 50%;
        background-position: -188px -380px;
        width: 46px;
        height: 32px;
        transform: translate(-52%, -47%); }
    &_dot-1 {
        background-position: -165px -427px;
        width: 28px;
        height: 28px; }
    &_dot-2 {
        background-position: -408px -380px;
        width: 28px;
        height: 31px; }
    &_dot-3 {
        background-position: -375px -380px;
        width: 31px;
        height: 31px; }
    &_dot-4 {
        background-position: -72px -427px;
        width: 30px;
        height: 32px; }
    &_dot-5 {
        background-position: -307px -380px;
        width: 32px;
        height: 32px; }
    &_dot-6 {
        background-position: -135px -427px;
        width: 28px;
        height: 28px; } }
.dot_active {
    .icon_dot-1 {
        background-position: -423px -335px;
        width: 34px;
        height: 34px; }
    .icon_dot-2 {
        background-position: -423px -296px;
        width: 34px;
        height: 37px; }
    .icon_dot-3 {
        background-position: -423px -177px;
        width: 37px;
        height: 37px; }
    .icon_dot-4 {
        background-position: -423px -216px;
        width: 36px;
        height: 38px; }
    .icon_dot-5 {
        background-position: -423px -137px;
        width: 38px;
        height: 38px; }
    .icon_dot-6 {
        background-position: 0px -427px;
        width: 34px;
        height: 34px; } }
.content {
    &__text {
        padding: 0 20px;
        line-height: 1.6; }
    &__header {
        background: #eeeefb;
        color: #373737;
        text-align: center;
        padding: 25px 0 25px 0;
        margin-bottom: 20px;
        font-size: 36px;
        font-weight: bold; }
    &__wrapper {
        margin: 30px 0 70px; } }

.callback-popup {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9998;
    overflow: hidden;
    &__form {
        position: relative;
        top: 150px;
        width: 460px;
        padding: 20px;
        height: 200px;
        margin: 0 auto;
        text-align: center;
        background: rgba( 249, 249, 249, 0.96 ); }
    &__input {
        border: none;
        display: block;
        margin: 10px auto;
        padding: 0 15px;
        width: 370px;
        height: 40px; }

    &__header {
        text-align: left;
        font-size: 32px; }
    &_close {
        display: none; } }

.close-btn {
    position: absolute;
    top: 0;
    right: 0;
    height: 30px;
    line-height: 30px;
    color: #ffffff;
    width: 100px;
    background: #6394b4;
    cursor: pointer; }
.breadcrumbs {
    line-height: 60px;
    padding-left: 30px;
    background: #f2f2f2;
    a {
        color: #6394B4; } }
