.contacts {
    display: flex;
    &__item {
        flex: 1;
        p {
            width: 277px;
            padding: 10px 0; }
        .icon {
            position: relative;
            float: left;
            margin: 8px 50px 8px 10px;
            &:after {
                position: absolute;
                display: block;
                content: "";
                background-image: url(/img/sprite.png);
                background-position: -423px 0px;
                width: 43px;
                height: 49px;
                top: 49%;
                left: 50%;
                transform: translate(-51%, -49%); } } }
    &__wrapper {
        position: relative;
        display: flex;
        .contacts__map {
            flex: 2;
            margin-bottom: 0; }
        .close {
            top: 10%;
            right: 0.7%;
            left: unset;
            transform: translate(-0.7%, -10%);
            width: 100px;
            height: 30px;
            padding: 0;
            overflow: hidden;
            .contacts__close-btn::before {
                content: 'Открыть'; } } }
    &__header {
        text-align: center;
        margin-bottom: 15px; }
    &__block {
        background: rgba(255,255,255, .8);
        padding: 50px 30px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        flex-direction: column;
        flex: 1;
        padding-left: 25px;
        transition: 1.5s all; } }
.contacts__close-btn {
    position: absolute;
    top: 0;
    right: 0;
    height: 30px;
    line-height: 30px;
    color: #ffffff;
    width: 100px;
    background: #6394b4;
    cursor: pointer;
    &::before {
        content: 'Закрыть';
        text-align: center;
        padding-left: 20px; } }

.home_services-block {
    margin-bottom: 20px; }

.slider {
    width: 100%;
    display: table;
    position: relative;
    overflow: hidden;
    &__list {
        width: calc(100% * 6);
        transition: 1s;
        transition-timing-function: ease-in-out; }
    &-item {
        width: calc(100%/6);
        display: inline;
        float: left;
        list-style: none;
        position: relative;
        &__img {
            width: 100%; }
        &__text {
            position: absolute;
            color: #fff;
            font-weight: 800;
            font-size: 30px;
            text-transform: uppercase;
            top: 40%;
            left: 50px; } } }
.slider-dots {
    position: absolute;
    bottom: 20px;
    min-width: 100%;
    text-align: center;
    &__item {
        vertical-align: middle;
        cursor: pointer;
        display: inline-block;
        background-image: url(../img/sprite.png);
        background-position: -306px -290px;
        width: 54px;
        height: 63px;
        margin: 10px 18px;
        position: relative;
        .icon {
            position: absolute;
            right: 12px;
            top: 15px; } } }
.dot_active {
    background-position: 0px -290px;
    width: 76px;
    height: 88px;
    .icon {
        right: 21px;
        top: 27px; } }
.support {
    &__list {
        max-width: 700px;
        margin: 0 auto; }
    &__item {
        display: inline-block;
        text-align: center;
        width: 200px;
        height: 160px;
        vertical-align: top;
        margin-bottom: 20px; }
    &__img {
        position: relative;
        display: inline-block;
        width: 76px;
        height: 88px;
        background-image: url(../img/support-item-bg.png);
        background-repeat: no-repeat; }
    &__num {
        font-size: 12px;
        font-weight: bold;
        position: relative;
        top: 1px;
        left: 19px; } }
